'use client';

import React, { useEffect, useMemo, useState } from 'react';
import {
  Accordion,
  AccordionItem,
  AccordionTrigger,
  AccordionContent,
} from '@/components/ui/accordion';

type Request = {
  id: string;
  title: string;
  beneficiaryRequestText: unknown; // JSON
  status: string;
  createdAt: string;
  beneficiary?: { firstName: string; lastName: string };
};

type RequestResponse = {
  id: string;
  responseId: string; // FK → BeneficiaryRequest.id
  responderId: string;
  responseText: unknown; // JSON
  respondedAt?: string;
  responder?: { id: string; firstName: string; lastName: string; email?: string };
};

function toPlain(val: unknown): string {
  if (val == null) return '';
  if (typeof val === 'string') return val;
  if (Array.isArray(val)) return val.map(toPlain).filter(Boolean).join('\n').trim();
  if (typeof val === 'object') {
    const o = val as Record<string, unknown>;
    if (Array.isArray((o as any).ops)) {
      try {
        return ((o as any).ops as any[])
          .map((op) => (typeof op?.insert === 'string' ? op.insert : ''))
          .join('')
          .trim();
      } catch {}
    }
    if ((o as any).type === 'doc' && Array.isArray((o as any).content)) {
      const walk = (n: any): string =>
        !n
          ? ''
          : n.type === 'text' && typeof n.text === 'string'
          ? n.text
          : Array.isArray(n.content)
          ? n.content.map(walk).join('')
          : '';
      try {
        return ((o as any).content as any[]).map(walk).join('').trim();
      } catch {}
    }
    for (const k of ['text', 'message', 'body', 'value'])
      if (typeof o[k] === 'string') return String(o[k]);
    try {
      const joined = Object.values(o).map(toPlain).filter(Boolean).join('\n').trim();
      if (joined) return joined;
    } catch {}
  }
  try {
    return JSON.stringify(val, null, 2);
  } catch {
    return String(val);
  }
}

function capitalizeFirstWord(input: string): string {
  if (!input) return '';
  const m = input.match(/[A-Za-z]/);
  if (!m) return input;
  const i = m.index as number;
  return input.slice(0, i) + input.charAt(i).toUpperCase() + input.slice(i + 1);
}

const BeneficiaryRequestsAccordion: React.FC<{ className?: string }> = ({ className = '' }) => {
  const [requests, setRequests] = useState<Request[]>([]);
  const [responses, setResponses] = useState<RequestResponse[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    let cancelled = false;
    (async () => {
      try {
        const [reqRes, respRes] = await Promise.all([
          fetch('/api/beneficiaryRequests'),
          fetch('/api/beneficiaryRequestResponse'),
        ]);
        const [reqJson, respJson] = await Promise.all([
          reqRes.ok ? reqRes.json() : [],
          respRes.ok ? respRes.json() : [],
        ]);
        if (!cancelled) {
          setRequests(Array.isArray(reqJson) ? reqJson : []);
          setResponses(Array.isArray(respJson) ? respJson : []);
        }
      } catch {
        if (!cancelled) {
          setRequests([]);
          setResponses([]);
        }
      } finally {
        if (!cancelled) setLoading(false);
      }
    })();
    return () => {
      cancelled = true;
    };
  }, []);

  const responsesByRequestId = useMemo(() => {
    const map = new Map<string, RequestResponse[]>();
    for (const r of responses) {
      if (!r?.responseId) continue;
      if (!map.has(r.responseId)) map.set(r.responseId, []);
      map.get(r.responseId)!.push(r);
    }
    for (const [k, arr] of map) {
      arr.sort((a, b) => {
        const ta = a.respondedAt ? new Date(a.respondedAt).getTime() : 0;
        const tb = b.respondedAt ? new Date(b.respondedAt).getTime() : 0;
        return tb - ta;
      });
      map.set(k, arr);
    }
    return map;
  }, [responses]);

  return (
    <div
      className={[
        'max-w-none w-full',
        'bg-transparent dark:bg-transparent',
        'shadow-none drop-shadow-none ring-0 outline-none border-0 rounded-none',
        className,
      ].join(' ')}
    >
      <h2 className="text-2xl font-bold text-center px-6 py-4 text-gray-900 dark:text-gray-100">
        Recent Beneficiary Requests
      </h2>

      {loading && (
        <div className="px-6 pb-6 text-center text-base text-gray-500 dark:text-gray-400">
          Loading...
        </div>
      )}
      {!loading && requests.length === 0 && (
        <div className="px-6 pb-6 text-center text-base text-gray-500 dark:text-gray-400">
          No beneficiary requests yet.
        </div>
      )}

      {!loading && requests.length > 0 && (
        <Accordion
          type="single"
          collapsible
          className="px-6 pb-6 divide-y divide-gray-200 dark:divide-gray-700"
        >
          {requests.slice(0, 5).map((req) => {
            const reqResponses = responsesByRequestId.get(req.id) || [];
            const requestText = capitalizeFirstWord(toPlain(req.beneficiaryRequestText));
            const requestTitle = capitalizeFirstWord(req.title);

            return (
              <AccordionItem key={req.id} value={req.id} className="py-0">
                <AccordionTrigger className="group w-full flex items-center gap-3 py-4 text-left text-xl font-semibold text-gray-900 dark:text-gray-100 [&>svg]:hidden focus:outline-none focus:ring-0">
                  <span className="flex-shrink-0">
                    <svg
                      className="h-7 w-7 text-gray-900 dark:text-gray-100"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth={2}
                    >
                      <circle cx="12" cy="12" r="10" className="stroke-current" />
                      <line x1="8" y1="12" x2="16" y2="12" className="stroke-current" />
                      <line
                        x1="12"
                        y1="8"
                        x2="12"
                        y2="16"
                        className="stroke-current transition-opacity duration-200 group-data-[state=open]:opacity-0"
                      />
                    </svg>
                  </span>
                  <span className="text-left flex-1">{requestTitle}</span>
                </AccordionTrigger>

                <AccordionContent className="pl-9 pr-0 pb-4 text-lg space-y-4 text-gray-900 dark:text-gray-100">
                  <div className="text-base text-gray-600 dark:text-gray-300">
                    {req.beneficiary
                      ? `By ${req.beneficiary.firstName} ${req.beneficiary.lastName}`
                      : 'By Beneficiary'}{' '}
                    • {new Date(req.createdAt).toLocaleDateString()}
                  </div>

                  <pre className="whitespace-pre-wrap break-words text-lg">{requestText}</pre>

                  <div className="text-base capitalize text-gray-700 dark:text-gray-300">
                    {req.status}
                  </div>

                  {reqResponses.length > 0 && (
                    <div className="mt-2 pl-4 border-l border-gray-200 dark:border-gray-700 space-y-4">
                      {reqResponses.map((resp) => {
                        const respText = capitalizeFirstWord(toPlain(resp.responseText));
                        return (
                          <div key={resp.id} className="space-y-2">
                            <div className="text-base text-gray-600 dark:text-gray-300">
                              {resp.responder
                                ? `Response by ${resp.responder.firstName} ${resp.responder.lastName}`
                                : 'Response'}{' '}
                              {resp.respondedAt && (
                                <>• {new Date(resp.respondedAt).toLocaleDateString()}</>
                              )}
                            </div>
                            <pre className="whitespace-pre-wrap break-words text-lg">
                              {respText || '(no response text)'}
                            </pre>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </AccordionContent>
              </AccordionItem>
            );
          })}
        </Accordion>
      )}
    </div>
  );
};

export default BeneficiaryRequestsAccordion;
