'use client';

import React, { useEffect, useState } from 'react';
import {
  Accordion,
  AccordionItem,
  AccordionTrigger,
  AccordionContent,
} from '@/components/ui/accordion';

type Message = {
  id: string;
  title: string;
  beneficiaryMessageText: any; // allow string or JSON
  status: string;
  createdAt: string;
  beneficiary?: { firstName: string; lastName: string };
};

function toPlain(val: unknown): string {
  if (val == null) return '';
  if (typeof val === 'string') return val;
  if (Array.isArray(val)) return val.map(toPlain).filter(Boolean).join('\n').trim();
  if (typeof val === 'object') {
    const o = val as Record<string, unknown>;
    if (Array.isArray((o as any).ops)) {
      try {
        return ((o as any).ops as any[])
          .map((op) => (typeof op?.insert === 'string' ? op.insert : ''))
          .join('')
          .trim();
      } catch {}
    }
    if ((o as any).type === 'doc' && Array.isArray((o as any).content)) {
      const walk = (n: any): string =>
        !n
          ? ''
          : n.type === 'text' && typeof n.text === 'string'
          ? n.text
          : Array.isArray(n.content)
          ? n.content.map(walk).join('')
          : '';
      try {
        return ((o as any).content as any[]).map(walk).join('').trim();
      } catch {}
    }
    for (const k of ['text', 'message', 'body', 'value'])
      if (typeof o[k] === 'string') return String(o[k]);
    try {
      const joined = Object.values(o).map(toPlain).filter(Boolean).join('\n').trim();
      if (joined) return joined;
    } catch {}
  }
  try {
    return JSON.stringify(val, null, 2);
  } catch {
    return String(val);
  }
}

function capitalizeFirstWord(input: string): string {
  if (!input) return '';
  const m = input.match(/[A-Za-z]/);
  if (!m) return input;
  const i = m.index as number;
  return input.slice(0, i) + input.charAt(i).toUpperCase() + input.slice(i + 1);
}

const BeneficiaryMessagesAccordion: React.FC<{ className?: string }> = ({ className = '' }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/beneficiaryMessages')
      .then((r) => r.json())
      .then((d) => setMessages(Array.isArray(d) ? d : []))
      .finally(() => setLoading(false));
  }, []);

  return (
    <div
      className={[
        'max-w-none w-full',
        'bg-transparent dark:bg-transparent',
        'shadow-none drop-shadow-none ring-0 outline-none border-0 rounded-none',
        className,
      ].join(' ')}
    >
      <h2 className="text-2xl font-bold px-6 py-4 text-center text-gray-900 dark:text-gray-100">
        Recent Beneficiary Messages
      </h2>

      {loading && (
        <div className="px-6 pb-6 text-center text-base text-gray-500 dark:text-gray-400">
          Loading...
        </div>
      )}
      {!loading && messages.length === 0 && (
        <div className="px-6 pb-6 text-center text-base text-gray-500 dark:text-gray-400">
          No beneficiary messages yet.
        </div>
      )}

      {!loading && (
        <Accordion
          type="single"
          collapsible
          className="px-6 pb-2 divide-y divide-gray-200 dark:divide-gray-700"
        >
          {messages.slice(0, 5).map((msg) => {
            const title = capitalizeFirstWord(msg.title ?? '');
            const contentText = capitalizeFirstWord(toPlain(msg.beneficiaryMessageText));
            return (
              <AccordionItem key={msg.id} value={msg.id} className="py-0">
                <AccordionTrigger className="group w-full flex items-center gap-3 py-4 text-left text-xl font-semibold text-gray-900 dark:text-gray-100 [&>svg]:hidden focus:outline-none focus:ring-0">
                  <span className="flex-shrink-0">
                    <svg
                      className="h-7 w-7 text-gray-900 dark:text-gray-100"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth={2}
                    >
                      <circle cx="12" cy="12" r="10" className="stroke-current" />
                      <line x1="8" y1="12" x2="16" y2="12" className="stroke-current" />
                      <line
                        x1="12"
                        y1="8"
                        x2="12"
                        y2="16"
                        className="stroke-current transition-opacity duration-200 group-data-[state=open]:opacity-0"
                      />
                    </svg>
                  </span>
                  <span className="text-left flex-1">{title}</span>
                </AccordionTrigger>

                <AccordionContent className="pl-9 pr-0 pb-4 text-lg space-y-4 text-gray-900 dark:text-gray-100">
                  <div className="text-base text-gray-600 dark:text-gray-300">
                    {msg.beneficiary
                      ? `By ${msg.beneficiary.firstName} ${msg.beneficiary.lastName}`
                      : 'By Beneficiary'}{' '}
                    • {new Date(msg.createdAt).toLocaleDateString()}
                  </div>
                  <div className="whitespace-pre-wrap break-words">{contentText}</div>
                  <div className="text-base capitalize text-gray-700 dark:text-gray-300">
                    {msg.status}
                  </div>
                </AccordionContent>
              </AccordionItem>
            );
          })}
        </Accordion>
      )}
    </div>
  );
};

export default BeneficiaryMessagesAccordion;
