'use client';

import React from 'react';
import {
  Accordion,
  AccordionItem,
  AccordionTrigger,
  AccordionContent,
} from '@/components/ui/accordion';

type FAQAccordionProps = { className?: string };

// Capitalize first alphabetical character, preserving leading whitespace
function capitalizeFirstWord(input: string): string {
  if (!input) return '';
  const m = input.match(/[A-Za-z]/);
  if (!m) return input;
  const i = m.index as number;
  return input.slice(0, i) + input.charAt(i).toUpperCase() + input.slice(i + 1);
}

type FAQItem = { question: string; answer: React.ReactNode | string };

const FAQS: FAQItem[] = [
  {
    question: 'What is GoGirls ICT Initiative?',
    answer: (
      <>
        GoGirls ICT Initiative is a nonprofit based in Juba, South Sudan, founded in 2015 and
        registered officially in 2019...
      </>
    ),
  },
  {
    question: 'What is the vision of GoGirls ICT Initiative?',
    answer: (
      <>
        Their vision is to: <br />
        <span className="italic">
          “Spark imaginations and curiosity in generations of problem solvers…”
        </span>
      </>
    ),
  },
  {
    question: 'What is their mission?',
    answer: (
      <>
        Their mission is summarized in three actions:
        <ul className="list-disc pl-5 mt-2">
          <li>Engage</li>
          <li>Educate</li>
          <li>Empower</li>
        </ul>
      </>
    ),
  },
  {
    question: 'What are GoGirls ICT’s core values?',
    answer: (
      <>
        Creativity, Innovation, Collaboration, Open-Mindedness, Flexibility, Responsibility,
        Consistency, Positivity.
      </>
    ),
  },
];

const FAQAccordion: React.FC<FAQAccordionProps> = ({ className = '' }) => {
  return (
    <div
      className={[
        'max-w-none w-full',
        // fully flat in both themes
        'bg-transparent dark:bg-transparent',
        'shadow-none drop-shadow-none ring-0 outline-none border-0',
        'rounded-none',
        className,
      ].join(' ')}
    >
      <h2 className="text-2xl font-bold px-6 py-4 text-center text-gray-900 dark:text-gray-100">
        Frequently Asked Questions (FAQ)
      </h2>

      <Accordion
        type="single"
        collapsible
        // only subtle between-item dividers (no top/bottom edge)
        className="px-6 pb-6 divide-y divide-gray-200/80 dark:divide-gray-700/60"
      >
        {FAQS.map((faq, idx) => {
          const questionText = capitalizeFirstWord(faq.question ?? '');
          const answerContent =
            typeof faq.answer === 'string' ? capitalizeFirstWord(faq.answer) : faq.answer;

          return (
            <AccordionItem key={faq.question} value={`${idx}`} className="py-0">
              <AccordionTrigger className="group w-full flex items-center gap-3 py-4 text-left text-xl font-semibold text-gray-900 dark:text-gray-100 [&>svg]:hidden focus:outline-none focus:ring-0">
                <span className="flex-shrink-0">
                  <svg
                    className="h-7 w-7 text-gray-900 dark:text-gray-100"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth={2}
                  >
                    <circle cx="12" cy="12" r="10" className="stroke-current" />
                    <line x1="8" y1="12" x2="16" y2="12" className="stroke-current" />
                    <line
                      x1="12"
                      y1="8"
                      x2="12"
                      y2="16"
                      className="stroke-current transition-opacity duration-200 group-data-[state=open]:opacity-0"
                    />
                  </svg>
                </span>
                <span className="text-left flex-1">{questionText}</span>
              </AccordionTrigger>

              <AccordionContent className="pl-9 pr-0 pb-4 text-lg text-gray-900 dark:text-gray-100">
                {answerContent}
              </AccordionContent>
            </AccordionItem>
          );
        })}
      </Accordion>
    </div>
  );
};

export default FAQAccordion;
